<template>
  <div>
    <v-app>
      <v-container grid-list-xl text-xs-center>
        <v-alert type="error" dismissible :value="showErro" @dismissed="showErro = false"
          >{{ erroMsg }}
        </v-alert>
        <v-layout row wrap>
          <v-flex xs4 offset-xs4>
            <v-card dark color="#0353cd" elevation-24 style="padding: 20px; border-radius: 50px;">
              <v-card-text>
                <h4 class="display-2 font-weight-bold">Login</h4>
                <hr />
                <br />
                <v-form>
                  <v-text-field
                    clearable
                    v-model="username"
                    label="Username"
                    type="text"
                    @click:prepend="prependClicked"
                    color="white"
                    @click:append-outer="appendClicked"
                    counter="10"
                  >
                  </v-text-field>
                  <v-text-field
                    clearable
                    v-model="password"
                    label="Password"
                    type="password"
                    @click:prepend="prependClicked"
                    color="white"
                    @click:append-outer="appendClicked"
                  >
                  </v-text-field>
                </v-form>
                <br />
                <v-btn color="#ffff" class="green--text" @click="enterClick" large>Entrar</v-btn>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-app>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  el: '#login',
  data() {
    return {
      username: '',
      password: '',
      showErro: false,
      dismissSecs: 5,
      dismissCountDown: 0,
      erroMsg: 'Usuário ou senha inválidos',
    };
  },
  methods: {
    showFail() {
      this.showErro = true;
    },
    async login() {
      const response = await axios({
        method: 'post',
        url: 'https://server-cli.herokuapp.com/v1/auth/login',
        data: {
          username: this.username,
          password: this.password,
        },
      });
      // eslint-disable-next-line no-empty
      console.log(JSON.stringify(response));
      if (response.data.login === 'Invalid') {
        console.log(JSON.stringify(response.data));
        this.showFail();
      } else {
        localStorage.setItem('user', JSON.stringify(response.data));
        this.$router.push('/');
      }
    },

    prependClicked() {
      console.log('prepend clicked');
    },
    appendClicked() {
      console.log('append clicked', this.username);
    },
    enterClick() {
      this.login();
      //
    },
  },
  created() {
    this.showErro = false;
  },
};
</script>
